// Font Sizes
$base-font-size: 18px;
$em-base: $base-font-size;
$base-font-family: 'PT Sans', sans-serif;
$heading-font-family: $base-font-family;

// font weight
$f-ubold: 900; // Extra Black, Fat, Poster or Ultra Black
$f-black: 800; // Black, Extra Bold or Heavy
$f-bold: 700; // Bold
$f-sbold: 600; // Semibold, Demibold
$f-med: 500; //  Medium
$f-norm: 400; //  Normal or Regular
$f-light: 300; // Book or Demi
$f-thin: 200; // 200 Light or Thin
$f-ulight: 100; // 100 Extra Light or Ultra Light

// Line height
$base-line-height: 1.1;
$heading-line-height: 1.2;

// Other Sizes
$width: 980px;
$footer: 115px;
$footer_margin: $footer - 2 * $footer;
$base-border-radius: 3px;
$base-spacing: $base-line-height * 1em;
$small-spacing: $base-spacing / 2;
$base-z-index: 0;

// Colors
$brown: #c69f6e;
$light-brown: #dfb268;
$dark-brown: #251c16;
$green: #67b20e;
$orange: orange;
$red: red;
$blue: #477dca;
$dark-gray: #333;
$medium-gray: #999;
$light-gray: #ddd;

// Font Colors
$base-font-color: $brown;
$action-color: $green;
$base-text-shadow: 0px 5px 3px rgba(0, 0, 0, 0.51);

// Border
$base-border-color: $light-gray;
$base-border: 1px solid $base-border-color;

// Background Colors
$base-background-color: #fff;
$secondary-background-color: tint($base-border-color, 75%);

// Forms
$form-box-shadow: inset 0 1px 3px rgba(#000, .06);
$form-box-shadow-focus: $form-box-shadow, 0 0 5px adjust-color($action-color, $lightness: -5%, $alpha: -.3);

// Animations
$trs: all .15s;
$transition: background-color 0.7s, color 1s, opacity 0.5s;
$base-duration: 150ms;
$base-timing: ease;
$base-transition: background-color $base-duration * 5 $base-timing, color $base-duration * .7 $base-timing, opacity $base-duration * .3 $base-timing;


.bg-brown {
  background-color: #31160a;
  background-color: rgba(49, 22, 10, .7);
}
