/*
== malihu jquery custom scrollbar plugin ==
Plugin URI: http://manos.malihu.gr/jquery-custom-content-scroller
*/

/*
CONTENTS:
  1. BASIC STYLE - Plugin's basic/essential CSS properties (normally, should not be edited).
  2. VERTICAL SCROLLBAR - Positioning and dimensions of vertical scrollbar.
  3. HORIZONTAL SCROLLBAR - Positioning and dimensions of horizontal scrollbar.
  4. VERTICAL AND HORIZONTAL SCROLLBARS - Positioning and dimensions of 2-axis scrollbars.
  5. TRANSITIONS - CSS3 transitions for hover events, auto-expanded and auto-hidden scrollbars.
  6. SCROLLBAR COLORS, OPACITY AND BACKGROUNDS
    6.1 THEMES - Scrollbar colors, opacity, dimensions, backgrounds etc. via ready-to-use themes.
*/

/*
------------------------------------------------------------------------------------------------------------------------
1. BASIC STYLE
------------------------------------------------------------------------------------------------------------------------
*/

.mCustomScrollbar {
  -ms-touch-action: pinch-zoom;
      touch-action: pinch-zoom;
  /* direct pointer events to js */
  &.mCS_no_scrollbar,
  &.mCS_touch_action {
    -ms-touch-action: auto;
        touch-action: auto;
  }
}

.mCustomScrollBox {
  /* contains plugin's markup */
  position: relative;

  overflow: hidden;

  max-width: 100%;
  height: 100%;

  outline: none;

  direction: ltr;
}

.mCSB_container {
  /* contains the original content */
  overflow: hidden;

  width: auto;
  height: auto;
}

/*
------------------------------------------------------------------------------------------------------------------------
2. VERTICAL SCROLLBAR
y-axis
------------------------------------------------------------------------------------------------------------------------
*/

.mCSB_inside > .mCSB_container {
  margin-right: 30px;
}

.mCSB_container.mCS_no_scrollbar_y.mCS_y_hidden {
  margin-right: 0;
}

/* non-visible scrollbar */

.mCS-dir-rtl > .mCSB_inside > .mCSB_container {
  /* RTL direction/left-side scrollbar */
  margin-right: 0;
  margin-left: 30px;
  &.mCS_no_scrollbar_y.mCS_y_hidden {
    margin-left: 0;
  }
}

/* RTL direction/left-side scrollbar */

.mCSB_scrollTools {
  /* contains scrollbar markup (draggable element, dragger rail, buttons etc.) */
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: auto;

  width: 16px;
  height: auto;
}

.mCSB_outside + .mCSB_scrollTools {
  right: -26px;
}

/* scrollbar position: outside */

.mCS-dir-rtl > {
  .mCSB_inside > .mCSB_scrollTools {
    /* RTL direction/left-side scrollbar */
    right: auto;
    left: 0;
  }
  .mCSB_outside + .mCSB_scrollTools {
    /* RTL direction/left-side scrollbar */
    right: auto;
    left: 0;
    left: -26px;
  }
}

/* RTL direction/left-side scrollbar (scrollbar position: outside) */

.mCSB_scrollTools {
  .mCSB_draggerContainer {
    /* contains the draggable element and dragger rail markup */
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    height: auto;
  }
  a + .mCSB_draggerContainer {
    margin: 20px 0;
  }
  .mCSB_draggerRail {
    width: 2px;
    height: 100%;
    margin: 0 auto;

    border-radius: 16px;
  }
  .mCSB_dragger {
    /* minimum dragger height */
    z-index: 1;

    width: 100%;
    height: 30px;
    /* the draggable element */

    cursor: pointer;
    .mCSB_dragger_bar {
      /* the dragger element */
      position: relative;

      width: 4px;
      height: 100%;
      margin: 0 auto;

      text-align: center;

      border-radius: 16px;
    }
  }
}

.mCSB_scrollTools_vertical.mCSB_scrollTools_onDrag_expand {
  .mCSB_dragger.mCSB_dragger_onDrag_expanded .mCSB_dragger_bar,
  .mCSB_draggerContainer:hover .mCSB_dragger .mCSB_dragger_bar {
    width: 12px;
    /* auto-expanded scrollbar */
  }
  .mCSB_dragger.mCSB_dragger_onDrag_expanded + .mCSB_draggerRail,
  .mCSB_draggerContainer:hover .mCSB_draggerRail {
    width: 8px;
    /* auto-expanded scrollbar */
  }
}

.mCSB_scrollTools {
  .mCSB_buttonUp {
    position: absolute;

    display: block;
    overflow: hidden;

    width: 100%;
    height: 20px;
    margin: 0 auto;

    cursor: pointer;
  }
  .mCSB_buttonDown {
    position: absolute;
    bottom: 0;

    display: block;
    overflow: hidden;

    width: 100%;
    height: 20px;
    margin: 0 auto;

    cursor: pointer;
  }
}

/*
------------------------------------------------------------------------------------------------------------------------
3. HORIZONTAL SCROLLBAR
x-axis
------------------------------------------------------------------------------------------------------------------------
*/

.mCSB_horizontal {
  &.mCSB_inside > .mCSB_container {
    margin-right: 0;
    margin-bottom: 30px;
  }
  &.mCSB_outside > .mCSB_container {
    min-height: 100%;
  }
  > .mCSB_container.mCS_no_scrollbar_x.mCS_x_hidden {
    margin-bottom: 0;
  }
}

/* non-visible scrollbar */

.mCSB_scrollTools.mCSB_scrollTools_horizontal {
  top: auto;
  right: 0;
  bottom: 0;
  left: 0;

  width: auto;
  height: 16px;
}

.mCustomScrollBox + .mCSB_scrollTools {
  &.mCSB_scrollTools_horizontal, + .mCSB_scrollTools.mCSB_scrollTools_horizontal {
    bottom: -26px;
  }
}

/* scrollbar position: outside */

.mCSB_scrollTools.mCSB_scrollTools_horizontal {
  a + .mCSB_draggerContainer {
    margin: 0 20px;
  }
  .mCSB_draggerRail {
    width: 100%;
    height: 2px;
    margin: 7px 0;
  }
  .mCSB_dragger {
    left: 0;

    width: 30px;
    /* minimum dragger width */
    height: 100%;
    .mCSB_dragger_bar {
      width: 100%;
      height: 4px;
      margin: 6px auto;
    }
  }
}

.mCSB_scrollTools_horizontal.mCSB_scrollTools_onDrag_expand {
  .mCSB_dragger.mCSB_dragger_onDrag_expanded .mCSB_dragger_bar,
  .mCSB_draggerContainer:hover .mCSB_dragger .mCSB_dragger_bar {
    height: 12px;
    /* auto-expanded scrollbar */
    margin: 2px auto;
  }
  .mCSB_dragger.mCSB_dragger_onDrag_expanded + .mCSB_draggerRail,
  .mCSB_draggerContainer:hover .mCSB_draggerRail {
    height: 8px;
    /* auto-expanded scrollbar */
    margin: 4px 0;
  }
}

.mCSB_scrollTools.mCSB_scrollTools_horizontal {
  .mCSB_buttonLeft,
  .mCSB_buttonRight {
    position: absolute;

    display: block;
    overflow: hidden;

    width: 20px;
    height: 100%;
    margin: 0 auto;

    cursor: pointer;
  }
  .mCSB_buttonLeft {
    left: 0;
  }
  .mCSB_buttonRight {
    right: 0;
  }
}

/*
------------------------------------------------------------------------------------------------------------------------
4. VERTICAL AND HORIZONTAL SCROLLBARS
yx-axis
------------------------------------------------------------------------------------------------------------------------
*/

.mCSB_container_wrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  overflow: hidden;

  width: auto;
  height: auto;
  margin-right: 30px;
  margin-bottom: 30px;
  > .mCSB_container {
    box-sizing: border-box;
    padding-right: 30px;
    padding-bottom: 30px;
  }
}

.mCSB_vertical_horizontal > .mCSB_scrollTools {
  &.mCSB_scrollTools_vertical {
    bottom: 20px;
  }
  &.mCSB_scrollTools_horizontal {
    right: 20px;
  }
}

/* non-visible horizontal scrollbar */

.mCSB_container_wrapper {
  &.mCS_no_scrollbar_x.mCS_x_hidden + .mCSB_scrollTools.mCSB_scrollTools_vertical {
    bottom: 0;
  }
  &.mCS_no_scrollbar_y.mCS_y_hidden + .mCSB_scrollTools ~ .mCSB_scrollTools.mCSB_scrollTools_horizontal {
    right: 0;
  }
}

/* non-visible vertical scrollbar/RTL direction/left-side scrollbar */

.mCS-dir-rtl > {
  .mCustomScrollBox.mCSB_vertical_horizontal.mCSB_inside > {
    .mCSB_scrollTools.mCSB_scrollTools_horizontal {
      right: 0;
      left: 20px;
    }
    .mCSB_container_wrapper.mCS_no_scrollbar_y.mCS_y_hidden + .mCSB_scrollTools ~ .mCSB_scrollTools.mCSB_scrollTools_horizontal {
      left: 0;
    }
  }
  .mCSB_inside > .mCSB_container_wrapper {
    /* RTL direction/left-side scrollbar */
    margin-right: 0;
    margin-left: 30px;
  }
}

/* RTL direction/left-side scrollbar */

/* non-visible scrollbar/RTL direction/left-side scrollbar */

.mCSB_container_wrapper {
  &.mCS_no_scrollbar_y.mCS_y_hidden > .mCSB_container {
    padding-right: 0;
  }
  &.mCS_no_scrollbar_x.mCS_x_hidden > .mCSB_container {
    padding-bottom: 0;
  }
}

.mCustomScrollBox.mCSB_vertical_horizontal.mCSB_inside > .mCSB_container_wrapper {
  &.mCS_no_scrollbar_y.mCS_y_hidden {
    margin-right: 0;
    /* non-visible scrollbar */
    margin-left: 0;
  }
  &.mCS_no_scrollbar_x.mCS_x_hidden {
    margin-bottom: 0;
  }
}

/* non-visible horizontal scrollbar */

/*
------------------------------------------------------------------------------------------------------------------------
5. TRANSITIONS
------------------------------------------------------------------------------------------------------------------------
*/

.mCSB_scrollTools {
  transition: opacity .2s ease-in-out, background-color .2s ease-in-out;
  .mCSB_dragger .mCSB_dragger_bar,
  .mCSB_buttonUp,
  .mCSB_buttonDown,
  .mCSB_buttonLeft,
  .mCSB_buttonRight {
    transition: opacity .2s ease-in-out, background-color .2s ease-in-out;
  }
}

.mCSB_scrollTools_vertical.mCSB_scrollTools_onDrag_expand {
  .mCSB_dragger_bar,
  .mCSB_draggerRail {
    transition: width .2s ease-out .2s, height .2s ease-out .2s,  margin-left .2s ease-out .2s, margin-right .2s ease-out .2s,  margin-top .2s ease-out .2s, margin-bottom .2s ease-out .2s, opacity .2s ease-in-out, background-color .2s ease-in-out;
  }
}

.mCSB_scrollTools_horizontal.mCSB_scrollTools_onDrag_expand {
  .mCSB_dragger_bar,
  .mCSB_draggerRail {
    transition: width .2s ease-out .2s, height .2s ease-out .2s,  margin-left .2s ease-out .2s, margin-right .2s ease-out .2s,  margin-top .2s ease-out .2s, margin-bottom .2s ease-out .2s, opacity .2s ease-in-out, background-color .2s ease-in-out;
  }
}

