    opacity: 1;
  }
}

.mCustomScrollBox:hover {
  > .mCSB_scrollTools, ~ .mCSB_scrollTools {
    opacity: 1;
  }
}

.mCS-autoHide:hover > .mCustomScrollBox {
  > .mCSB_scrollTools, ~ .mCSB_scrollTools {
    opacity: 1;
  }
}

.mCSB_scrollTools {
  .mCSB_draggerRail {
    background-color: #000;
    background-color: rgba(0, 0, 0, .4);
  }
  .mCSB_dragger {
    .mCSB_dragger_bar {
      background-color: #fff;
      background-color: rgba(255, 255, 255, .75);
    }
    &:hover .mCSB_dragger_bar {
      background-color: #fff;
      background-color: rgba(255, 255, 255, .85);
    }
    &:active .mCSB_dragger_bar,
    &.mCSB_dragger_onDrag .mCSB_dragger_bar {
      background-color: #fff;
      background-color: rgba(255, 255, 255, .9);
    }
  }
  .mCSB_buttonUp,
  .mCSB_buttonDown,
  .mCSB_buttonLeft,
  .mCSB_buttonRight {
    opacity: .4;
    background-image: url(mCSB_buttons.png);
    /* css sprites */
    background-repeat: no-repeat;

        filter: 'alpha(opacity=40)';
    -ms-filter: 'alpha(opacity=40)';
  }
  .mCSB_buttonUp {
    background-position: 0 0;
    /*
    sprites locations
    light: 0 0, -16px 0, -32px 0, -48px 0, 0 -72px, -16px -72px, -32px -72px
    dark: -80px 0, -96px 0, -112px 0, -128px 0, -80px -72px, -96px -72px, -112px -72px
    */
  }
  .mCSB_buttonDown {
    background-position: 0 -20px;
    /*
    sprites locations
    light: 0 -20px, -16px -20px, -32px -20px, -48px -20px, 0 -92px, -16px -92px, -32px -92px
    dark: -80px -20px, -96px -20px, -112px -20px, -128px -20px, -80px -92px, -96px -92px, -112 -92px
    */
  }
  .mCSB_buttonLeft {
    background-position: 0 -40px;
    /*
    sprites locations
    light: 0 -40px, -20px -40px, -40px -40px, -60px -40px, 0 -112px, -20px -112px, -40px -112px
    dark: -80px -40px, -100px -40px, -120px -40px, -140px -40px, -80px -112px, -100px -112px, -120px -112px
    */
  }
  .mCSB_buttonRight {
    background-position: 0 -56px;
    /*
    sprites locations
    light: 0 -56px, -20px -56px, -40px -56px, -60px -56px, 0 -128px, -20px -128px, -40px -128px
    dark: -80px -56px, -100px -56px, -120px -56px, -140px -56px, -80px -128px, -100px -128px, -120px -128px
    */
  }
  .mCSB_buttonUp:hover,
  .mCSB_buttonDown:hover,
  .mCSB_buttonLeft:hover,
  .mCSB_buttonRight:hover {
    opacity: .75;

        filter: 'alpha(opacity=75)';
    -ms-filter: 'alpha(opacity=75)';
  }
  .mCSB_buttonUp:active,
  .mCSB_buttonDown:active,
  .mCSB_buttonLeft:active,
  .mCSB_buttonRight:active {
    opacity: .9;

        filter: 'alpha(opacity=90)';
    -ms-filter: 'alpha(opacity=90)';
  }
}

/* theme: "dark" */

